@import url('https://fonts.googleapis.com/css2?family=Cedarville+Cursive&family=Inspiration&family=Lato:wght@300;400;700&family=Montserrat:wght@300&family=Open+Sans:wght@300;400;500;600;800&family=Poppins:ital,wght@0,200;0,300;0,400;0,500;1,300&family=Raleway:wght@300;400;500;600;900&family=Shalimar&family=Shrikhand&family=Source+Sans+Pro:wght@300&display=swap');


.movie img{
    width: 100%;
    height: 100%;
}

.movie{
    width: 165px;
    height: 235px;
    /* border: 2px solid red; */
    cursor: pointer;
    margin: 6px;
    background-color: #1e2931;
    color: white;
    position: relative;
    box-shadow: 0 0 1em rgb(0 0 0 / 30%)
    
}

.movie h4{
    width: 100%;
    height: 100%;
    font-size: 15px;
    font-weight: 600;
    text-transform: uppercase;
    font-family: 'Lato', sans-serif;
    font-family: 'Montserrat', sans-serif;
    padding: 12px;
    display: none;
    position:absolute;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 3, 5, 0.534);
    color: white;
    align-items: center;
    justify-content: center;

    /* z-index: 9999; */
    /* opacity: 99; */

}

.movie:hover h4{
    display: flex;
}